<template>
	<div id="header">
		<!--  -->
        <header>
            <!-- logo -->
            <div class="am-fl tpl-header-logo">
                <a href="javascript:;"><img src="../assets/img/logo_11.png" height="571" width="1129" alt=""></a>
            </div>
            <!--  -->
            <div class="tpl-header-fluid">
                <!--  -->
                <!-- <div class="am-fl tpl-header-switch-button am-icon-list">
                    <span>
                    </span>
                </div> -->
                <!-- -->
                <div class="am-fr tpl-header-navbar">
                    <ul>
                        <!--  -->
                        <li class="am-text-sm tpl-header-navbar-welcome">
                            <img src="../assets/img/user011.png" alt="">
                            <a href="javascript:;">, <span>{{user_name}}</span> </a>
                        </li>

                        <!--  -->
                        <li class="am-text-sm">
                            <a href="javascript:;" @click="logOut($event)">
                                <span class="am-icon-sign-out"></span> 
                            </a>
                        </li>
                    </ul>
                </div>
            </div>

        </header>
	</div>
</template>

<script>
    export default {
        name: 'Header',
        data () {
            return {
                user_name: $.cookie('name')
            }
        },
        methods: {
            logOut (e) {
                Public.addEditFn(e, '', this.htm(), function () {
                    Public.Ajax('user/logout', {}, 'GET', function(res){
                        $.cookie('token', null);
                        $.cookie('name', null);
                        window.location.href = '#/login';
                    }); 
                });
            },
            htm () {
                return '<div class="am-modal am-modal-prompt" tabindex="-1" id="add-edit-modal">'+
                    '<div class="am-modal-dialog">'+
                    '<div class="am-modal-hd" style="text-align: center;padding: 20px 0px;">!</div>'+
                    '<div class="am-modal-footer">'+
                    '<span class="am-modal-btn" data-am-modal-cancel></span>'+
                    '<span class="am-modal-btn" data-am-modal-confirm></span>'+
                    '</div>'+
                    '</div>'+
                    '</div>';
            }
        }
    };
</script>



